import React, {Component} from 'react';
import {FaAngleRight, FaAngleDown} from 'react-icons/fa';
import GatheringMap from '../Components/GatheringMap';

class GatheringPoints extends Component {
    constructor(){
        super()
        this.state = {
            hasForceUpdated: false
        }
        this.performForceUpdate = this.performForceUpdate.bind(this)
    }

    componentDidMount(){
        this.performForceUpdate()
            .then(() => {this.forceUpdate()})
    }

    performForceUpdate(){
        return new Promise(resolve => setTimeout(() => {
            this.setState({
                hasForceUpdated: true
            })
            resolve()}
            , 50));
    }

    render() {
        return (
            <>
            {this.props.territories.map((territory, key) => {
                if(territory !== null) {
                    return (
                        <React.Fragment key={key}>
                            <h2 onClick={this.props.toggleTerritories}>{this.props.selectedTerritory.includes(territory) ? <FaAngleDown /> : <FaAngleRight />} {territory} ({this.props.gatheringPoints.filter(x => x.TerritoryType.Map.PlaceNameRegion.Name === territory).length} nodes)</h2>
                            {this.props.regions.map((region, key) => {
                                if(this.props.gatheringPoints.filter(x => x.TerritoryType.Map.PlaceNameRegion.Name === territory && x.TerritoryType.Map.PlaceName.Name === region).length > 0) {
                                    return (
                                        <div key={key} className={this.props.selectedTerritory.includes(territory) ? '' : 'invisible'}>
                                        <h3 onClick={this.props.toggleRegions}>{this.props.selectedRegion.includes(region) ? <FaAngleDown /> : <FaAngleRight />} {region}</h3>
                                            <GatheringMap
                                                url={`https://xivapi.com${this.props.gatheringPoints.filter(x => x.TerritoryType.Map.PlaceName.Name === region)[0].TerritoryType.Map.MapFilename}`}
                                                nodes={this.props.gatheringPoints.filter(x => x.TerritoryType.Map.PlaceName.Name === region)}
                                                mapData={this.props.mapData}
                                                selectedRegion={this.props.selectedRegion}
                                            />
                                        </div>
                                    )
                                } else {
                                    return <div key={key} className="invisible"/>
                                }
                            })}                                                
                        </React.Fragment>
                    );
                } else {
                    return <div key={key} className="invisible"/>
                }
            })}
            </>
        );
    }
}

export default GatheringPoints;