import React, {Component} from 'react';
import './App.css';

import HomePage from './pages/HomePage';
import CraftingPage from './pages/CraftingPage';
import GatheringPage from './pages/GatheringPage';
import FishingPage from './pages/FishingPage';
import NavBar from './NavBar';

class App extends Component {
  constructor() {
    super();
    this.state = {
      searchString: '',
      searchResults: [],
      selectedObj: [],
      gatheringItemResults: null,
      craftingItemResults: [],
      fishingItemResults: null,
      lastIndex: 0,
      mapData: [],
      fishingSpotData: [],
      gatheringPointData: null,
      territories: [],
      regions: []
    };
    this.updateSearchTerm = this.updateSearchTerm.bind(this);
    this.performSearch = this.performSearch.bind(this);
    this.updateSelectedObj = this.updateSelectedObj.bind(this);
    this.lookupItem = this.lookupItem.bind(this);
    this.lookupRecipe = this.lookupRecipe.bind(this);
  }

  componentDidMount() {
    this.getData();
  }

  getData = () => {
    return new Promise(resolve => setTimeout(() => {
      const mapData = [];

      fetch('./MappyData.json', 
        {
          headers: {
            'Content-Type': 'application/json',
            'Accept': 'application/json'
          }
        })
        .then(response => response.json())
        .then(results => results.map(item => {
          mapData.push(item);
          return item;
        }))

      const fishingSpotData = [];
      const urlFishing = 'https://xivapi.com/fishingspot?limit=1000&columns=ID,Item0TargetID,Item1TargetID,Item2TargetID,Item3TargetID,Item4TargetID,Item5TargetID,Item6TargetID,Item7TargetID,Item8TargetID,Item9TargetID,PlaceName.Name,TerritoryType.PlaceName.Name,TerritoryType.PlaceNameRegion.Name,TerritoryType.Map.MapFilename,X,Z';

      fetch(urlFishing, {mode: 'cors'})
        .then(response => response.json())
        .then(results => results.Results.map(item => {
          fishingSpotData.push(item);
          return item;
        }))

      this.setState({
        mapData: mapData,
        fishingSpotData: fishingSpotData
      });
    
      resolve()
    }
    , 0));
  }

  updateSearchTerm(searchTerm) {
    this.setState({searchString: searchTerm});
  }

  performSearch() {
    const url = `https://xivapi.com/search?string=${this.state.searchString.replace(' ', '%20')}`

    this.setState({
      gatheringItemResults: null,
      craftingItemResults: [],
      fishingItemResults: null,
      searchResults: [],
      gatheringPointData: null,
      territories: [],
      regions: []
    });

    fetch(url, {mode: 'cors'})
      .then(response => response.json())
      .then(result => {
        for(var i = 1; i <= result.Pagination.PageTotal; i++) {
          const paginatedURL = `https://xivapi.com/search?string=${this.state.searchString.replace(' ', '%20')}&page=${i}&private_key=${process.env.REACT_APP_XIV_API_KEY}`
    
          fetch(paginatedURL, {mode: 'cors'})
            .then(response => response.json())
            .then(result => {
              let items = result['Results'].map(item => {
                item.itemId = this.state.lastIndex;
                this.setState({lastIndex: this.state.lastIndex + 1});
                return item;
              });
    
              this.setState({
                searchResults: items
              });
            });
        }
      });
  }

  lookupItem(obj) {
    const url = `https://xivapi.com/item/${obj.ID}`
    const gatheringPoints = [];
    const territories = [];
    const regions = [];
    const gatheringPointBaseIDs = [];
    const gatheringPointIDs = [];

    this.setState({
      gatheringItemResults: null,
      fishingItemResults: null
    })

    fetch(url, {mode: 'cors'})
      .then(response => response.json())
      .then(result => {
        if(result.GameContentLinks.hasOwnProperty('GatheringItem')){
          const urlGatheringItemID = `https://xivapi.com/item/${result.ID}?columns=GameContentLinks.GatheringItem.Item`

          fetch(urlGatheringItemID, {mode: 'cors'})
            .then(response => response.json())
            .then(result2 => { 
              const urlGatheringPointBaseIDs = `https://xivapi.com/gatheringitem/${result2.GameContentLinks.GatheringItem.Item}?columns=GameContentLinks.GatheringPointBase`
              
              fetch(urlGatheringPointBaseIDs, {mode: 'cors'})
                .then(response => response.json())
                .then(result3 => {
                  for(let item in [1, 2, 3, 4, 5, 6, 7]) {
                    if(result3.GameContentLinks.GatheringPointBase.hasOwnProperty(`Item${item}`)){
                      for(var i = 0; i < result3.GameContentLinks.GatheringPointBase[`Item${item}`].length; i++){
                        gatheringPointBaseIDs.push(result3.GameContentLinks.GatheringPointBase[`Item${item}`][i])
                      }
                    }
                  }
                })
                .then(() => {
                  const urlGatheringPointIDs = `https://xivapi.com/gatheringpointbase?ids=${gatheringPointBaseIDs.join()}&columns=GameContentLinks.GatheringPoint.GatheringPointBase`

                  fetch(urlGatheringPointIDs, {mode: 'cors'})
                      .then(response => response.json())
                      .then(result4 => result4.Results.map(item => {
                          for(var i = 0; i < item.GameContentLinks.GatheringPoint.GatheringPointBase.length; i++){
                              if(!gatheringPointIDs.includes(item.GameContentLinks.GatheringPoint.GatheringPointBase[i])){
                                gatheringPointIDs.push(item.GameContentLinks.GatheringPoint.GatheringPointBase[i])
                              }
                          }
                          return item;
                      }))
                  .then(() => {
                    const url = `https://xivapi.com/GatheringPoint?ids=${gatheringPointIDs.join()}limit=-1&columns=ID,GatheringPointBase.ID,GatheringPointBase.GatheringLevel,GatheringPointBase.GatheringType.ID,GatheringPointBase.GatheringType.Name,GatheringPointBase.GatheringType.IconMain,TerritoryType.Map.ID,TerritoryType.Map.PlaceNameRegion.Name,TerritoryType.Map.PlaceName.Name,GatheringPointBase.Item0.Item.ID,GatheringPointBase.Item1.Item.ID,GatheringPointBase.Item2.Item.ID,GatheringPointBase.Item3.Item.ID,GatheringPointBase.Item4.Item.ID,GatheringPointBase.Item5.Item.ID,GatheringPointBase.Item6.Item.ID,GatheringPointBase.Item7.Item.ID,PlaceName.Name,TerritoryType.Map.MapFilename`;

                    fetch(url, {mode: 'cors'})
                      .then(response => response.json())
                      .then(results => results.Results.map(item => {
                        if(item) {
                          gatheringPoints.push(item)
                      
                          if(!territories.includes(item.TerritoryType.Map.PlaceNameRegion.Name)) {                
                            territories.push(item.TerritoryType.Map.PlaceNameRegion.Name);
                          }
                          if(!regions.includes(item.TerritoryType.Map.PlaceName.Name)) {
                            regions.push(item.TerritoryType.Map.PlaceName.Name);
                          }
                        }
                        return item;
                      }));
                  });
              });
          });

          this.setState({
            gatheringItemResults: [
              result.Name,
              result.ID,
              result.GameContentLinks.GatheringItem.Item[0],
              result.Icon
            ],
            gatheringPointData: gatheringPoints,
            territories: territories,
            regions: regions
          });
        } else if(result.GameContentLinks.hasOwnProperty('FishingSpot')){
          this.setState({
            fishingItemResults: result
          });
        } else {
          this.setState({
            gatheringItemResults: [`${result.Name} cannot be gathered`],
            fishingItemResults: [`${result.Name} cannot be fished`]
          });
        }
      });
  }

  lookupRecipe(obj) {
    const url = `https://xivapi.com/recipe/${obj.ID}?columns=ID,Icon,RecipeLevelTable,AmountIngredient0,AmountIngredient1,AmountIngredient2,AmountIngredient3,AmountIngredient4,AmountIngredient5,AmountIngredient6,AmountIngredient7,AmountIngredient8,AmountIngredient9,AmountResult,ClassJob.Abbreviation,ClassJob.Icon,ItemIngredient0,ItemIngredient1,ItemIngredient2,ItemIngredient3,ItemIngredient4,ItemIngredient5,ItemIngredient6,ItemIngredient7,ItemIngredient8,ItemIngredient9,ItemIngredientRecipe0,ItemIngredientRecipe1,ItemIngredientRecipe2,ItemIngredientRecipe3,ItemIngredientRecipe4,ItemIngredientRecipe5,ItemIngredientRecipe6,ItemIngredientRecipe7,ItemIngredientRecipe8,ItemIngredientRecipe9,ItemResult`

    this.setState({
      selectedObjResults: [],
      craftingItemResults: [],
      fishingItemResults: null
    });

    fetch(url, {mode: 'cors'})
      .then(response => response.json())
      .then(result => {
        this.setState({
          craftingItemResults: result
        });
      });
  }

  updateSelectedObj(obj) {
    this.setState({
      selectedObj: obj,
      gatheringItemResults: null,
      craftingItemResults: [],
      fishingItemResults: null,
      gatheringPointData: null,
      territories: [],
      regions: []
    })

    if(obj['UrlType'] === 'Recipe') {
      this.lookupRecipe(obj);
    } else {
      this.lookupItem(obj);
    }
  }

  render () {
    return (
      <>
        <div className="app">
          <NavBar />
          <div id="page-body">
            <HomePage 
              updateSearchTerm={this.updateSearchTerm}
              updateSelectedObj={this.updateSelectedObj} 
              performSearch={this.performSearch} 
              searchString={this.state.searchString}
              searchResults={this.state.searchResults}
              selectedObj={this.state.selectedObj}
            />
            <CraftingPage
              obj={this.state.craftingItemResults}
              lookupItem={this.lookupItem}
            />
            <GatheringPage
              obj={this.state.gatheringItemResults}
              mapData={this.state.mapData}
              gatheringPointData={this.state.gatheringPointData}
              territories={this.state.territories}
              regions={this.state.regions}
            />
            <FishingPage 
              obj={this.state.fishingItemResults}
              fishingSpotData={this.state.fishingSpotData}
            />
          </div>
        </div>
      </>
    );
  }
}

export default App;